import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import axios from 'axios'
import VueAxios from 'vue-axios'
import PublicLayout from './layouts/PublicLayout'
  import Inicio from './pages/Inicio'
  import Noticias from './pages/Noticias'
  import InformacionPublica from './pages/InformacionPublica'
  import Institucional from './pages/Institucional'
  import TrabajandoPorTi from './pages/TrabajandoPorTi'
  import TramitesYGestiones from './pages/TramitesYGestiones'
  import DenunciasCiudadanas from './pages/DenunciasCiudadanas'
import PrivateLayout from './layouts/PrivateLayout'

Vue.config.productionTip = false
Vue.use(VueRouter)
Vue.use(VueAxios, axios)

const routes=new VueRouter({
  base:"/muni_tlsc/",
  mode:"history",
  routes:[
    {path:"/",component:PublicLayout,children:[
      {path:"",component:Inicio,name:"Inicio"},
      {path:"noticias",component:Noticias,name:"Noticias"},
      {path:"institucional",component:Institucional,name:"Institucional"},
      {path:"informacion-publica",component:InformacionPublica,name:"Informacion Publica"},
      {path:"trabajando-por-ti",component:TrabajandoPorTi,name:"Trabajando Por Ti"},
      {path:"tramites-gestiones",component:TramitesYGestiones,name:"Tramites y Gestiones"},
      {path:"denuncias-ciudadanas",component:DenunciasCiudadanas,name:"Denuncias Ciudadanas"},
    ]},
    {path:"/",component:PrivateLayout,children:[

    ]}
  ]
});

new Vue({
  router:routes,
  render: h => h(App),
}).$mount('#app')
